.app{
	height: 100vh;
	
	.sections{
		width: 100%;
		height: calc(100vh - 70px);
		position: relative;
		top: 70px;
		scroll-snap-type: y mandatory;

		//scrollbar
		scrollbar-width: none;
		&::-webkit-scrollbar{
			display: none;
		}

		>*{
			width: 100%;
			height: calc(100vh - 70px);
			scroll-snap-align: start;
		}
	}
}